.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #eee;
    border-radius: 10px;
    box-shadow: 1px 1px 3px #ccc;
    padding: 16px;
    left: 50%;
    top: 50%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
    }
}